import Vue from "vue";
import Router from "vue-router";
import Landing from "./views/Landing.vue";
import Home from "./views/Home.vue";
import SelectOS from "./views/SelectOS.vue";
import Download from "./views/Download.vue";
import PreRegister from "./views/PreRegister.vue";
import Legal from "./views/Legal.vue";
import NewsLetter from "./views/NewsLetter.vue";
import Redirect from "./views/Redirect.vue";
import SignUp from "./views/SignUp.vue";
import Login from "./views/Login.vue";
import Uploader from "./views/Uploader.vue";
import Welcome from './views/Welcome.vue';
import UploadApp from './views/UploadApp.vue';
import SelectPlatform from './views/SelectPlatform.vue';
import AppLogin from "./views/AppLogin";
import UpgradeDone from "./views/UpgradeDone.vue";

Vue.use(Router);

export default new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes: [
        {
            path: "/redirect",
            name: "redirect",
            component: Redirect
        },
        {
            path: "/client_auth",
            name: "client_auth",
            component: AppLogin
        },
        {
            path: "/web",
            component: UploadApp,
            children: [
                {
                    path: "select-platform",
                    name: "SelectPlatform",
                    component: SelectPlatform
                },
                {
                    path: "welcome",
                    name: "welcome",
                    component: Welcome
                },
                {
                    path: "",
                    name: "uploader",
                    component: Uploader
                },
            ]
        },
        {
            path: "/",
            component: Home,
            children: [
                {
                    path: "",
                    name: "landing",
                    component: Landing
                },
                {
                    path: "select-os",
                    name: "select-os",
                    component: SelectOS
                },
                {
                    path: "download",
                    name: "download",
                    component: Download
                },
                {
                    path: "upgrade",
                    name: "upgrade",
                    component: PreRegister
                },
                {
                    path: "upgrade-done",
                    name: "upgrade-done",
                    component: UpgradeDone
                },
                {
                    path: "legal",
                    name: "legal",
                    component: Legal
                },
                {
                    path: "newsletter",
                    name: "newsletter",
                    component: NewsLetter
                },
                {
                    path: "sign-up",
                    name: "sign-up",
                    component: SignUp
                },
                {
                    path: "login",
                    name: "login",
                    component: Login
                },
                // Put the star route at the end
                {
                    path: "*",
                    name: "not-found",
                    component: Landing
                }
            ]
        }
    ],
    scrollBehavior() {
        return {x: 0, y: 0};
    }
});
