<template>
    <div id="pre-register">
        <div class="contents-limiter">
            <div class="columns">
                <div class="column plus-block is-6 is-offset-3">
                    <h4 class="h4">
                        Upgrade to BlackHole
                        <span class="plus">PLUS</span>.
                    </h4>
                    <div class="title-2">$5 Monthly</div>
                    <p class="subtitle-1">
                        Experience BlackHole full potential without limitations.
                    </p>

                    <div class="register" @keyup.enter="subscribe">
                        <input
                                v-model="email"
                                type="email"
                                id="EMAIL"
                                name="EMAIL"
                                autocomplete="off"
                                class="body-2"
                                placeholder="Email address"
                                data-required="true"
                                required
                        />
                        <div class="button-block">
                            <input
                                    form="sib-form"
                                    type="button"
                                    @click="subscribe"
                                    class="button-2"
                                    :class="{ loading: isAnimate, done: isDone }"
                                    :disabled="isAnimate || isDone"
                                    value="Upgrade"
                            />
                            <span :class="{ spiner: isAnimate }"></span>

                            <img
                                    src="../assets/done_24px.png"
                                    srcset="../assets/done_24px.png 1x, ../assets/done_24px_2x.png 2x"
                                    class="done-icon"
                                    v-show="isDone"
                                    alt="Done Icon"
                            />
                        </div>
                        <p class="error">{{ errors }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    name: "pre-register",
    data() {
        return {
            email: "",
            errors: "",
            clickTime: 0,
            currentTime: 0,
            isAnimate: false,
            isDone: false
        };
    },
    methods: {
        validEmail(email) {
            var regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return regex.test(email);
        },

        subscribe() {
            if (!this.email) {
                this.errors = "Email required.";

                this.$ga.event({
                    eventCategory: "submiter",
                    eventAction: "empty_email_error",
                    eventLabel: "plus_pre_register",
                    eventValue: 0
                });
            } else if (!this.validEmail(this.email)) {
                this.errors = "Invalid email address.";

                this.$ga.event({
                    eventCategory: "submiter",
                    eventAction: "email_validation_error",
                    eventLabel: "plus_pre_register",
                    eventValue: 0
                });
            } else {
                this.clickTime = new Date().getTime();
                this.isAnimate = true;
                axios.post("https://police.blackhole.run/api/event", {
                    type: "Upgrade",
                    meta: JSON.stringify({
                        email: this.email
                    })
                });
                axios
                    .post("https://police.blackhole.run/api/subscribe", {email: this.email})
                    .then(() => {
                        this.currentTime = new Date().getTime();
                        const timeDiffrence = this.currentTime - this.clickTime;

                        if (timeDiffrence > 1000) {
                            setTimeout(() => {
                                this.isAnimate = false;
                                this.isDone = true;
                            }, timeDiffrence);
                        } else {
                            this.isAnimate = true;
                            setTimeout(() => {
                                this.isAnimate = false;
                                this.isDone = true;
                            }, 1000);
                        }

                        window.location.href = "/upgrade-done";

                        this.$ga.event({
                            eventCategory: "submiter",
                            eventAction: "email_registering",
                            eventLabel: "plus_pre_register",
                            eventValue: 0
                        });
                    })
                    .catch(reason => {
                        this.currentTime = new Date().getTime();
                        const timeDiffrence = this.currentTime - this.clickTime;

                        if (timeDiffrence > 1000) {
                            setTimeout(() => {
                                this.isAnimate = false;
                                this.isDone = true;
                            }, timeDiffrence);
                        } else {
                            this.isAnimate = true;
                            setTimeout(() => {
                                this.isAnimate = false;
                                this.isDone = true;
                            }, 1000);
                        }

                        this.$ga.event({
                            eventCategory: "submiter",
                            eventAction: "email_registering",
                            eventLabel: "plus_pre_register",
                            eventValue: 0
                        });
                    });
            }
        }
    },

    watch: {
        email: function (newVal, oldVal) {
            if (this.email && this.errors === "Email required.") {
                this.errors = "";
            } else if (
                (!this.email && this.errors === "Invalid email address.") ||
                this.validEmail(this.email)
            ) {
                this.errors = "";
            }
        }
    }
};
</script>

